import { graphql, Link, useStaticQuery } from "gatsby"
import PropTypes from "prop-types"
import React from "react"
import styled from "styled-components"
import Img from "gatsby-image"
import { Location } from '@reach/router'

const THeader = styled.header`
  width: 100%;
  padding: 5px 10px 0 10px;
  position: fixed;
  top: 0;
  background-color: #f1f1f1;
  z-index: 2;
  border-bottom: 1px solid #bebebe;
  height: 60px;
`

const LinkRow = styled.div`
  height: 30px;
  text-align: center;
  display: inline-block;
  position: absolute;
  top: 15px;
  left: 50vw;
  transform: translateX(-50%);
`

const MainLink = styled.h1`
  display: inline-block;
  margin: 5px auto;
  font-size: 30px;
  a {
    color: #5c5c5c;
  }

`

export const BrandIcon = styled(Img)`
  width: 30px;
  display: inline-block;
  vertical-align: middle;
`

const HeaderUl = styled.ul`
  margin: 5px 0 1px;
  padding: 0;
`

const HeaderLi = styled.li`
   padding: 0 8px;
   display: inline-block;
   position: relative;
`

const HeaderLink = styled(Link)`
    padding: 10px;
    font-size: 16px;
    font-weight: ${props => (props.active ? `600` : `400`)};
    text-transform: uppercase;
    line-height: 42px;
    letter-spacing: .7px;
    color: ${props => (props.active ? `#748cc5` : `#6897ff`)};
    &:hover {
      color: #df4d55;
    }
`

const LinkShadow = styled.div`
  background-color: #748cc5;
  width:100px;
  height: 3px;
  transform:  ${props =>
    (contains(props.path, ['/blog','/post','/category'])?`translateX(73px) scaleX(0.6)`:
    (props.path.includes('/aboutme')?`translateX(170px) scaleX(1)`:
    (props.path.includes('/sandbox')?`translateX(290px) scaleX(1)`: 
    `translateX(-8px) scaleX(0.6)` 
      )))};
  transition: transform 0.2s ease-out;
`

const HeaderLiNx = styled(HeaderLi)`
   &:after {
    background-color: #333;
    border-radius: 100%;
    width: 4px;
    height: 4px;
    bottom: 18.8px;
    right: -3px;
    position: absolute;
    content: "";
  }
`



const HeaderTitle = styled.span`
  vertical-align: middle;
  font-size: 35px;
  line-height: 35px;
`

const Header = ({ siteTitle }) => {
  const data = useStaticQuery(
    graphql`
      query {
        titleIcon: file(relativePath: { eq: "siteicon.png" }) {
          childImageSharp {
            fluid(maxWidth: 30) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    `
  )
  return (
    <THeader>
      <MainLink>
        <Link to="/" >
          <BrandIcon fluid={data.titleIcon.childImageSharp.fluid}   />
          <HeaderTitle>{siteTitle}</HeaderTitle>
        </Link>
      </MainLink>
      <LinkRow>
        <Location>
          {({ location }) => {
            return(
              <>
              <HeaderUl>
                <HeaderLiNx>
                  <HeaderLink active={location.pathname==="/"?1:0}  to="/" >Home</HeaderLink>
                </HeaderLiNx>
                <HeaderLiNx>
                  <HeaderLink active={(contains(location.pathname, ['/blog','/post','/category']))?1:0 } to="/blog" >Blog</HeaderLink>
                </HeaderLiNx>
                <HeaderLiNx>
                  <HeaderLink active={location.pathname.includes("/aboutme")?1:0}  to="/aboutme" >About Me</HeaderLink>
                </HeaderLiNx>
                <HeaderLi>
                  <HeaderLink active={location.pathname.includes("/sandbox")?1:0}  to="/sandbox" >Sandbox</HeaderLink>
                </HeaderLi>
              </HeaderUl>
                <LinkShadow path={location.pathname}/>
              </>
            )
          }}
        </Location>


      </LinkRow>

    </THeader>
  )
}

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

function contains(target, pattern){
  var value = 0;
  pattern.forEach(function(word){
    value = value + target.includes(word);
  });
  return (value === 1)
}

export default Header
