import React, { useState, useEffect, createRef } from "react"
import styled from "styled-components"
import {
  InstantSearch,
  Hits,
  connectStateResults
} from "react-instantsearch-dom"
import algoliasearch from "algoliasearch/lite"
import PostHit from "./searchResult"


import Input from "./Input"

const Root = styled.div`
  position: relative;
  display: grid;
  grid-gap: 1em;
`


const HitsWrapper = styled.div`
  display: ${props => (props.show ? `flex` : `none`)};
  flex-direction: column;
  max-height: 90vh;
  width: 500px;
  margin-right: 15px;
`

//if have result return result, else "no result"
const Results = connectStateResults(
  ({ searchState: state, searchResults: res, children }) =>
    res && res.nbHits > 0 ? children : `No results for '${state.query}'`
)


//number of result
const Stats = connectStateResults(
  ({ searchResults: res }) =>
    res && res.nbHits > 0 && `${res.nbHits} result${res.nbHits > 1 ? `s` : ``}`
)

const ResultHeader = styled.div`
  background-color: #3c3c3c;
  color: white;
  display: block;
  font-size: larger;
  padding: 5px 10px;
`

//if clicking outside then remove?
const useClickOutside = (ref, handler, events) => {
  if (!events) events = [`mousedown`, `touchstart`]
  const detectClickOutside = event =>
    ref.current && !ref.current.contains(event.target) && handler()

  useEffect(() => {
    for (const event of events)
      document.addEventListener(event, detectClickOutside)

    return () => {
      for (const event of events)
        document.removeEventListener(event, detectClickOutside)
    }
  })
}


const ResultBox = styled(Hits)`
  background-color: #f5f5fa;
  height: 100%;
  overflow-y: auto;
  width: 100%;
  ul {
    list-style: none;
    padding: 5px;
    margin-top: 0;
  }
  li {
  background-color: white;
  margin: 20px 0;
  padding: 10px 5px;
  -webkit-box-shadow: 5px 5px 15px 0px rgba(136,136,136,0.37);
  -moz-box-shadow: 5px 5px 15px 0px rgba(136,136,136,0.37);
  box-shadow: 5px 5px 15px 0px rgba(136,136,136,0.37);
  
   &:hover{

   }
  }
`

const CloseButton = styled.div`
  float: right;
  display: inline-block;
  color: #f1f1f1;
  cursor: pointer;
  font-size: x-large;
`


export default function Search({ indices, collapse, hitsAsGrid }) {
  const ref= createRef()
  const [query, setQuery] = useState(``)
  const [focus, setFocus] = useState(false)
  const searchClient = algoliasearch(
    process.env.GATSBY_ALGOLIA_APP_ID,
    process.env.GATSBY_ALGOLIA_SEARCH_KEY
  )

  useClickOutside(ref, () => setFocus(false))
  return (
    <InstantSearch
      searchClient={searchClient}
      indexName="Blog"
      onSearchStateChange={({ query }) => setQuery(query)}
      root={{ Root, props: { ref } }}
    >
      <Input onFocus={() => setFocus(true)} {...{ collapse, focus }} />
      <HitsWrapper show={query.length > 0 && focus } >
        <ResultHeader>
          <Stats />
          <CloseButton onClick={() => setFocus(false)}>×</CloseButton>
        </ResultHeader>
        <Results>
          <ResultBox hitComponent={PostHit}/>
        </Results>
      </HitsWrapper>
    </InstantSearch>
  )
}