import React from "react"
import PropTypes from "prop-types"
import { StaticQuery, graphql, Link } from "gatsby"
import Header from "../components/header"
import "./layout.css"
import styled from "styled-components"
import Footer from "../components/footer"
import posed, { PoseGroup } from "react-pose"
import Search from "../components/Search"

const searchIndices = [
  { name: `Posts`, title: `Blog Posts`, hitComp: `PostHit` },
]


const timeout = 300
const delay = 350

const Main = styled.div`
  min-width: 800px;
  width: 50%;
  text-align: center;
  margin: 60px auto 0;
`

const Wrapper = styled.div`
  padding: 15px 0;
`

const MainBody = styled.div`
padding: 15px 0;
`
const PagingFooter = styled.div`
  width: 100%;
  z-index: 5;
  bottom: 0;
  left: 0;
  position: fixed;
  text-align: center;
  background-color: #f1f1f1;
  border-top: 1px solid #bebebe;
`

const Paging = styled.ul`

  margin: 5px auto;
  li {
    display: inline-block;
    padding: 5px 10px;
  }
`
const FooterLink = styled(Link)`
  display: inline-block;
  vertical-align: middle;
  color: #6897ff;
  border: 1px solid #6897ff;
  width: 30px;
  height: 30px;
  -webkit-box-shadow: 1px 2px 2px 0 rgba(0,0,0,0.26);
  -moz-box-shadow: 1px 2px 2px 0 rgba(0,0,0,0.26);
  box-shadow: 1px 2px 2px 0 rgba(0,0,0,0.26);
   transition: transform 0.2s ease-out;
  
  &:hover{
    transform: translateY( -3px) scale(1.03);
    transition: transform 0.3s ease-out;
    color: #df4d55;
    border: 1px solid #df4d55;
  }
  
`
//  color: #DD8825;
const ActiveLink = styled(FooterLink)`
  font-weight: bold;
  font-size: large;
  border: 1px solid #748cc5;
  width: 30px;
  height: 30px;
  color: #748cc5;
`
//  color: #e4e4e4;
const NonActiveLink= styled(FooterLink)`

`


const SearchBar = styled.div`
  position: fixed;
  top: 5px;
  left: 100vw;
  transform: translateX(-100%);
  z-index: 3;
  margin: 20px auto;
`


const RoutesContainer = posed(Wrapper)({
  enter: {
    delay: delay,
    opacity: 1,
    delayChildren: timeout,
    transition: { ease: 'easeOut', duration: timeout}
  },
  exit: {
    opacity: 0,
    delayChildren: timeout,
    transition: { ease: 'easeIn', duration: timeout }
  },
})


const Layout = ({ children, location, pageContext }) => (
  <StaticQuery
    query={graphql`query SiteTitleQuery {
        site {
          siteMetadata {
            title
          }
        }
      }`}
    render={data => (
      <MainBody>
        <Header siteTitle={data.site.siteMetadata.title} />
        {(pageContext.layout === "blog" || pageContext.layout==="post") && (
          <SearchBar>
            <Search collapse indices={searchIndices} />
          </SearchBar>
        )}
        <Main>
          <PoseGroup>
            <RoutesContainer key={location.pathname}>{children}</RoutesContainer>
          </PoseGroup>
        </Main>

        {pageContext.layout !== "blog" && (
          <Footer siteOwner={"Hourai Etealune"} />
        )}

        {pageContext.layout === "blog" && (
          <PagingFooter>
            <Paging>
              {pageContext.currentPage!==1 && (
                <li key={'prevpage'}>
                  <NonActiveLink to={pageContext.currentPage - 1 === 1 ? "/blog/" : "/blog/" + (pageContext.currentPage - 1).toString()} rel="prev">
                    &lt;
                  </NonActiveLink>
                </li>
              )}
              {Array.from({ length: pageContext.numPages }, (_, i) => (
                <li key={'page'+i}>
                  { pageContext.currentPage===i+1 && (
                    <ActiveLink to={`/blog/${i === 0 ? "" : i + 1}`}>
                      {i + 1}
                    </ActiveLink>
                  )}
                  { pageContext.currentPage!==i+1 && (
                    <NonActiveLink to={`/blog/${i === 0 ? "" : i + 1}`}>
                      {i + 1}
                    </NonActiveLink>
                  )}
                </li>
              ))}
              {pageContext.currentPage!==pageContext.numPages && (
                <li key={'nextpage'}>
                  <NonActiveLink to={"/blog/" + (pageContext.currentPage + 1).toString()} rel="next">
                    &gt;
                  </NonActiveLink>
                </li>
              )}
            </Paging>
          </PagingFooter>
        )}

      {/*  {pageContext.layout === "post" && (*/}
      {/*  <PagingFooter>*/}
      {/*    <Paging>*/}
      {/*      {pageContext.prev!==null && (*/}
      {/*        <li key={'prevpost'}>*/}
      {/*          <NonActiveLink to={pageContext.prev.fields.path} rel="prev" replace>*/}
      {/*            &lt;*/}
      {/*          </NonActiveLink>*/}
      {/*        </li>*/}
      {/*      )}*/}
      {/*      {pageContext.next!== null && (*/}
      {/*        <li key={'nextpost'}>*/}
      {/*          <NonActiveLink to={pageContext.next.fields.path} rel="next" replace>*/}
      {/*            &gt;*/}
      {/*          </NonActiveLink>*/}
      {/*        </li>*/}
      {/*      )}*/}
      {/*    </Paging>*/}
      {/*  </PagingFooter>*/}
      {/*)}*/}

      </MainBody>
    )}
  />
)
Layout.propTypes = {
    children: PropTypes.node.isRequired,
}
export default Layout